.body {
    display: flex;
    margin-top: 4rem;
    justify-content: center;
}

.Kassa {
    background-color: #161c24;
    width: 95%;
}

.inp_group {
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.inp_group input {
    width: 23%;
    background-color: transparent;
    border: none;
    height: 60%;
    font-size: 18px;
    border-bottom: 2px solid gray !important;
    color: white;
    outline: none;
}

.btn_group {
    width: 100%;
    text-align: right;
    margin: 1rem 0;
}

.btn_group button {
    width: 150px;
    height: 35px;
    font-size: 14px;
    background-color: transparent;
    outline: none;
    color: #fff;
    margin-right: 10px;
    border: 3px solid rgba(255, 255, 255, 0.281);

}

.report {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: white;
    width: 95%;
    height: 56px;
    margin: 1rem auto;

}

.report h1 {
    font-size: 0.875rem;
    margin: 0;
}

.report button {
    cursor: default !important;
    height: 35px;
    width: 10%;
    font-size: 12px;
    background-color: transparent;
    font-size: 14px;
    outline: none;
    margin: 0;
    color: #fff;
    border: 2px solid rgba(255, 255, 255, 0.425);
}

.report_body {
    background-color: rgb(33, 43, 54);
    color: rgb(255, 255, 255);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    box-shadow: rgb(0 0 0 / 20%) 0px 0px 2px 0px, rgb(0 0 0 / 12%) 0px 12px 24px -4px;
    border-radius: 16px;
}

.title {
    color: #fff;
    padding: 24px 24px 10px;
    font-size: 18px;
}

.th {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 97%;
    height: 56px;
    margin: 0 auto;
    background-color: rgba(145, 158, 171, 0.16);
    border-radius: 4px;
    color: rgb(145, 158, 171);
    line-height: 1.5rem;
    font-size: 0.875rem;
    font-family: "Public Sans", sans-serif;
    font-weight: 600;
}

.th p {
    margin: 0;
}

.name {
    width: 20%;
    color: #fff;
    margin-left: 10px;
    text-align: center;
    overflow-wrap: anywhere;
}

.product {
    text-align: center;
    overflow-wrap: anywhere;
    width: 20%;
    color: #fff;
}

.price {
    width: 15%;
    color: #fff;
    text-align: center;
    overflow-wrap: anywhere;

}

.comment {
    color: #fff;
    overflow-wrap: anywhere;
    width: 25%;
    text-align: center;


}

.status {
    color: #fff;
    width: 10%;
    text-align: center;
    overflow-wrap: anywhere;
}

@media(min-width:0px) and (max-width:600.9px) {
    .inp_group input {
        width: 90%;
        margin: 1rem 0;
        font-size: 16px;
    }
    .status {
       display: none;
    }
    .inp_group {
        justify-content: center;
    }

    .btn_group {
        width: 100%;
        justify-content: center;
        display: flex;

    }

    .btn_group button {
        width: 40%;
    }

    .report_body {
        width: 90%;
        box-sizing: border-box;
        margin: 0 auto;
        flex-wrap: wrap;
        overflow: auto;
        margin-bottom: 2rem;
        box-sizing: border-box;
    }
    .name,
    .product,
    .comment,
    .status,
    .price {
        text-align: center;
    }

    .title {
        padding: 10px 10px 5px;
        font-size: 14px;
    }
}